import React from 'react'
import style from './../../../App.module.css'
import gifka from './../../../other/img/3x.gif'

const GameCard = (props) => {
    return (
        <div className={style.card_container}>
                <h2>{props.gameData.title}</h2>
                <div>
                    <div className={style.top_block}>
                        <div>
                            <img src={props.gameData.thumbnail} />
                        </div>
                        <ul>
                            <li>{props.gameData.genre}</li>
                            <li>{props.gameData.developer}</li>
                            <li>{props.gameData.platform}</li>
                            <li>{props.gameData.developer}</li>
                            <li>{props.gameData.publisher}</li>
                            <li>{props.gameData.release_date}</li>
                            <li>{props.gameData.status}</li>
                        </ul>
                    </div>
                    <div>
                        <div>
                            <p>
                                {props.gameData.description}
                            </p>
                        </div>
                        <div>{props.gameData.screenshots.map(item => <img src={item} />)}</div>
                    </div>
                    <div>
                        <ul>MSQ:
                        <li>{props.gameData.minimum_system_requirements.graphics}</li>
                            <li>{props.gameData.minimum_system_requirements.memory}</li>
                            <li>{props.gameData.minimum_system_requirements.os}</li>
                            <li>{props.gameData.minimum_system_requirements.processor}</li>
                            <li>{props.gameData.minimum_system_requirements.storage}</li>
                        </ul>
                        <a href={props.gameData.game_url}>check</a>
                    </div>
                </div>
            </div>
    )
}

export default GameCard
